import React, { useState, useEffect } from "react";
import { Form, Button, message, Card, Input, Row, Select, DatePicker, Table } from "antd";
import TextArea from "antd/lib/input/TextArea";
import CustomerModal from "../components/CustomerModal";
import { connect, createDispatchHook } from "react-redux";
import { createOrder, getCustomerList, getMemo, resetProject } from "redux/actions/Project";
import { useHistory } from "react-router-dom";
import moment from "moment";
const ProductCreate = (props) => {
  const { loading, customerList, getCustomerList, resetProject, createOrder, orderPk, getMemo, memos } = props;
  const [visible, setVisible] = useState();
  const [fields, setFields] = useState();
  const [customer, setCustomer] = useState();
  const history = useHistory();
  const onFinish = (data) => {
    createOrder(data);
  };
  const showModal = () => {
    setVisible(true);
  };

  useEffect(() => {
    if (orderPk) {
      history.push({ pathname: "/app/sales/select", state: { orderPk } });
    }
  }, [orderPk]);

  const handleOk = (customer) => {
    if (customer) {
      setFields([
        {
          name: ["customer"],
          value: customer.id,
        },
        {
          name: ["customerName"],
          value: customer.name,
        },
        {
          name: ["receiver"],
          value: customer.name,
        },
        {
          name: ["address"],
          value: customer.address,
        },
      ]);
    }
    setCustomer(customer);
    setVisible(false);
  };

  const handleCancel = (e) => {
    setVisible(false);
  };

  const handleCustomer = () => {
    if (customer) {
      getMemo(customer.id);
    }
  };

  const column = [
    {
      title: "",
      dataIndex: "created",
      render: (_, record) => <span>{moment(record.created).format("YYYY-MM-DD")}</span>,
    },
    {
      title: " ",
      dataIndex: "sellerMemo",
    },
    {
      title: " ",
      dataIndex: "customerMemo",
    },
  ];

  useEffect(() => {
    getCustomerList();
    return () => {
      resetProject();
    };
  }, []);

  return (
    <>
      <Card title={" ()"}>
        <Form fields={fields} onFinish={onFinish}>
          <Form.Item name={"customer"} hidden>
            <Input></Input>
          </Form.Item>
          <Form.Item name={"created"} label={""} rules={[{ required: true, message: " " }]}>
            <DatePicker />
          </Form.Item>
          <Row justify="space-between">
            <Form.Item
              name={"customerName"}
              label={""}
              message={"hi"}
              rules={[{ required: true, message: " " }]}
            >
              <Input disabled></Input>
            </Form.Item>
            <Row>
              <Button type={"primary"} onClick={showModal}>
                 
              </Button>
            </Row>
          </Row>
          <Form.Item
            name={"receiver"}
            label={""}
            placeholder={"  "}
            rules={[{ required: true, message: " " }]}
          >
            <Input></Input>
          </Form.Item>
          <Form.Item name={"address"} label={""} rules={[{ required: true, message: " " }]}>
            <Input></Input>
          </Form.Item>
          <Form.Item name={"customerMemo"} initialValue={" "} label={" "}>
            <TextArea></TextArea>
          </Form.Item>
          <Form.Item name={"sellerMemo"} initialValue={" "} label={" "}>
            <TextArea></TextArea>
          </Form.Item>
          <Form.Item name={"isDelivered"} initialValue={true} label={" "}>
            <Select>
              <Select.Option value={true}> </Select.Option>
              <Select.Option value={false}> </Select.Option>
            </Select>
          </Form.Item>
          <Form.Item name={"isPaid"} initialValue={true} label={" "}>
            <Select>
              <Select.Option value={true}> </Select.Option>
              <Select.Option value={false}> </Select.Option>
            </Select>
          </Form.Item>
          <Row justify="end">
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Row>
        </Form>
      </Card>
      <CustomerModal
        visible={visible}
        customerList={customerList}
        showModal={showModal}
        handleOk={handleOk}
        handleCancel={handleCancel}
        getCustomerList={getCustomerList}
      />
      <Card title={`${customer ? `${customer.name} ` : "  ( )"}`}>
        <Row justify="end">
          <Button onClick={handleCustomer}> </Button>
        </Row>
        <Table columns={column} dataSource={memos ? memos : ""} rowKey={"id"} />
      </Card>
    </>
  );
};

const mapStateToProps = ({ project }) => {
  const { loading, customerList, orderPk, memos } = project;
  return { loading, customerList, orderPk, memos };
};

const mapDispatchToProps = {
  getCustomerList,
  resetProject,
  createOrder,
  getMemo,
};

export default connect(mapStateToProps, mapDispatchToProps)(ProductCreate);
